import { AnswerReply } from "./answer_reply.model";

export class Answer {
    public answer_id: Number;
    public post_id: Number;
    public answer_html: string;
    public answer_content: string;
    public answer_time: Date;
    public answer_votes: Number;
    public user_id: Number;
    public user_username:string
    public user_reputation:Number
    public replies: AnswerReply[];
    
    
  
    constructor(answer_id: Number,post_id: Number,answer_html: string,answer_content: string,answer_time: Date,answer_votes: Number,user_id: Number,user_username:string,user_reputation:Number) {
        this.answer_id=answer_id;
        this.post_id=post_id;
        this.answer_html=answer_html.replace("img","img class="+'"max"');
        this.answer_content=answer_content;
        this.answer_time=new Date (answer_time);
        this.answer_votes=answer_votes;
        this.user_id=user_id;
        this.user_username = user_username;
        this.user_reputation = user_reputation;


        this.replies = [];
  
    }
  
  }
  