import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { PostService } from '../../posts/post.service';
import { AuthService } from '../../auth/auth.service';
import { HelpService } from '../../help.service';
import { HttpClient } from '@angular/common/http';
import {ArticleService} from '../article.service';


@Component({
  selector: 'app-create-article',
  templateUrl: './create-article.component.html',
  styleUrls: ['./create-article.component.css']
})
export class CreateArticleComponent implements OnInit {


  s: string;
  html_content: string;

  htmllength = 0;
  contentlength = 0;
  htmlcontent;
  textcontent;
  title:string;

  constructor(private route: ActivatedRoute, private router: Router, private httpclient: HttpClient, private articleService: ArticleService, private authService: AuthService, private helper: HelpService) { }

  ngOnInit() {
    if (this.authService.getIsAuth()==false)
    {
    this.router.navigate(['/']);
    }

  }


  maxLength(e) {

    this.htmlcontent = e["html"];
    this.textcontent = e["text"];
    const rawtext = e["editor"]["root"]["textContent"];
    console.log(" html content is " + this.htmlcontent);
    console.log(" text content is " + this.textcontent);
    console.log(" raw text is  " + rawtext);
    this.htmllength = 0;
    this.contentlength = 0;
    try {
      this.contentlength = this.textcontent.length;
      this.htmllength = this.htmlcontent.length;
    }
    catch { }
    console.log(this.contentlength);
    console.log("html content length " + this.htmllength);


  }

  submit_article() {
    console.log(this.title);
    console.log (this.html_content);
    console.log(this.textcontent);
    this.articleService.submit_article(this.title,this.html_content,this.textcontent).subscribe(article_id => {

      console.log(article_id);
      {
        location.reload();
      }

    });
  }
}
