import { Injectable } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import { Router } from "@angular/router";
import { Subject,Observable, observable } from "rxjs";

import { Post } from "./post.model";
import {PostReply} from './post_reply.model'
import { Answer } from "./answer.model";
import { AnswerReply } from "./answer_reply.model";
import { Tag } from "./tag.model";

@Injectable({ providedIn: "root" })
// @Injectable()
export class PostService {

//   private authStatusListener = new Subject<boolean>();

  constructor(private httpclient: HttpClient, private router: Router) {}

  post_view(post_id:number){

    return   new Observable<any>(observer =>
    this.httpclient.post("http://localhost:3000/get_post",{post_id: post_id}).subscribe((data:any) => {
        console.log(data);
        let html = data.post.post_html;
        html=html.replace("img","img class="+'"max"')
        // html=html.replace("img","img style="+'"max-width:50px;"'+" ")
        console.log(html);
        let post = new Post(post_id,data.post.post_title,html,data.post.post_content,data.post.post_time,data.post.post_votes,data.post.user_id,data.post.user_username,data.post.user_reputation,data.post.post_views);
        const answers = data.answers;
        let post_replies = data.post_replies;
        let answers_replies = data.answers_replies;
        let tags = data.tags;
        const post_replies_ob = this.post_replies_arrange(post_replies);
        const answers_ob = this.answers_arrange(answers,answers_replies);
        const tags_obs = this.tags_arrange(tags);

        post.replies = post_replies_ob;
        post.answers = answers_ob;
        post.tags= tags_obs;
        console.log("TADADADA!!!");
        console.log(post);
        observer.next(post);
        
      })
    );

  }

  post_replies_arrange(post_replies){
    //   post_replies.map( reply =>
    //     new PostReply(reply.reply_id,reply.post_id,reply.reply_content,reply.reply_time,reply.reply_votes,reply.user_id,reply.user_username,reply.user_reputation));
    //     return post_replies[0];
    const post_replies_ob=[];
    post_replies.forEach(reply => {
        post_replies_ob.push(new PostReply(reply.reply_id,reply.post_id,reply.reply_content,reply.reply_time,reply.reply_votes,reply.user_id,reply.user_username,reply.user_reputation))
        
    });
    return post_replies_ob;
    
  }
  answers_arrange(answers,answers_replies){// this methods get 2 arrays: answers and replies to those answers. it makes 1 new array: answers, and for each answer the relevant replies
     const answers_ob=[];
     answers.forEach(answer => {
         answers_ob.push(new Answer (answer.answer_id,answer.post_id,answer.answer_html,answer.answer_content,answer.answer_time,answer.answer_votes,answer.user_id,answer.user_username,answer.user_reputation))
         answers_replies.forEach(reply => {
           console.log(reply.answer_id);
           console.log(answers_ob[answers_ob.length-1]);
            if (reply.answer_id == answers_ob[answers_ob.length-1].answer_id)
            {
              console.log("i am here!");
                answers_ob[answers_ob.length-1].replies.push(new AnswerReply (reply.reply_id,reply.answer_id,reply.reply_content,reply.reply_time,reply.reply_votes,reply.user_id,reply.user_username,reply.user_reputation));
                
            }
             
         }); 
         
     });
     return answers_ob;

  }
  tags_arrange(tags) {
    const tags_ob=[];
    tags.forEach(tag => {
      tags_ob.push(new Tag (tag.tag_id, tag.tag_name, tag.tagging_id, tag.tag_category,tag.tag_subcategory));
    });
    return tags_ob;
  }
  vote(message_type,message_id,vote_value) {
    return   new Observable<any>(observer => {
    console.log(message_type);
    this.httpclient.post("http://localhost:3000/insert_vote",{message_type,message_id,vote_value}).subscribe((data:any) => {
      console.log(data);
      observer.next(data.result);

    });
  }
);
  }

  submit_answer (post_id: Number,answer_html: string, answer_content: string) {
    return   new Observable<any>(observer => {
      this.httpclient.post("http://localhost:3000/create_answer",{post_id,answer_html, answer_content}).subscribe((data:any) => {
        observer.next(data.result);
    }
  );
      
    }
  );

  }

  submit_post_reply (post_id: Number, reply_content: string) {
    console.log(reply_content);
    return   new Observable<any>(observer => {
      this.httpclient.post("http://localhost:3000/create_post_reply",{post_id, reply_content}).subscribe((data:any) => {
        observer.next(data.result);
    }
  );
      
    }
  );

  }


  submit_answer_reply (answer_id: Number, reply_content: string) {
    console.log(reply_content);
    console.log("from service " + answer_id + "  " + reply_content);
    return   new Observable<any>(observer => {
      this.httpclient.post("http://localhost:3000/create_answer_reply",{answer_id, reply_content}).subscribe((data:any) => {
        observer.next(data.result);
    }
  );
      
    }
  );

  }

  submit_report (message_type: string, message_id: Number, report_type: string, report_message:string) {
    return   new Observable<any>(observer => {
      this.httpclient.post("http://localhost:3000/create_report",{message_type, message_id, report_type, report_message}).subscribe((data:any) => {
        observer.next(data.result);
    }
  );
      
    }
  );

  }

  submit_duplication_suggestion ( post_id: Number, original_post_id: Number) {
    return   new Observable<any>(observer => {
      this.httpclient.post("http://localhost:3000/create_duplication_suggestion",{ post_id, original_post_id}).subscribe((data:any) => {
        observer.next(data.result);
    }
  );
      
    }
  );

  }

  search_post (question: string) {
    console.log(question);
    return   new Observable<any>(observer => {
      this.httpclient.post("http://localhost:3000/search",{question}).subscribe((data:any) => {
        console.log(data);
        observer.next(data);
    }
  );
      
    }
  );

  }



  submit_searched_post (info) {
    console.log(info);
    return   new Observable<any>(observer => {
      this.httpclient.post("http://localhost:3000/searched_post",info).subscribe((data:any) => {
        observer.next(data.result);
    }
  );
      
    }
  );

  }

  load_home () {
    console.log();
    return   new Observable<any>(observer => {
      this.httpclient.get("http://localhost:3000/load_home").subscribe((data:any) => {
        observer.next(data.result);
    }
  );
      
    }
  );

  }

}