import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { PostService } from '../posts/post.service';
import { Post } from '../posts/post.model';
import { HelpService } from '../help.service';
import { post } from 'selenium-webdriver/http';
@Component({
  selector: 'app-search',
  templateUrl: './search.component.html',
  styleUrls: ['./search.component.css']
})
export class SearchComponent implements OnInit {
  result:[{}];
  begin:boolean;
  search_id:number;

  constructor(private route: ActivatedRoute, private postService: PostService,private helper:HelpService, private router: Router) { }

  ngOnInit() {
    this.route.queryParams.subscribe(params => {
      const question = params['value'];

      this.postService.search_post(question).subscribe(  result2 => {
        this.result = result2.result;
        this.search_id = result2.search_id[0].search_id;
        console.log(this.search_id);
       this.begin=true;
        console.log(this.result);

        this.result.forEach(post => {
          post["post_time"] = new Date (post["post_time"]);
          if (post["post_tags"])
          {
          post["tags"] = this.helper.string_to_array(post["post_tags"]);
          }
        });
  
      });
      
    });
    
  }

  navigate_post(post,rank) {
    console.log("DOES!");
    rank++;
    this.postService.submit_searched_post({post_id:post.post_id, search_id:this.search_id,post_rank:rank,post_score:post.score}).subscribe(  result => {
    }
  );
    
  }

}
