
export class AnswerReply {
    public reply_id: Number;
    public answer_id: Number;
    public reply_content: string;
    public reply_time: Date;
    public reply_votes: Number;
    public user_id: Number;
    public user_username: string;
    public user_reputation: number;
  
    constructor(reply_id: Number,answer_id: Number,reply_content: string,reply_time: Date,reply_votes: Number,user_id: Number,user_username: string,user_reputation: number) {
        this.reply_id=reply_id;
        this.answer_id=answer_id;
        this.reply_content=reply_content;
        this.reply_time=new Date( reply_time);
        this.reply_votes=reply_votes;
        this.user_id=user_id;
        this.user_username = user_username;
        this.user_reputation = user_reputation;
  
    }
  
  }
  