import { Component, OnInit } from '@angular/core';
import {HttpClient} from '@angular/common/http';
import { NgForm } from '@angular/forms';
import { HelpService } from '../help.service';
import { AuthService } from '../auth/auth.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {
  // private todate = new Date();
  private todate= new Date("2/2/2005"); //random date, will be changed by the current date from the server
  

  constructor(private httpclient: HttpClient,private helper:HelpService,private authService: AuthService,private router: Router) { }

  ngOnInit() {

    if (this.authService.getIsAuth())// registered users should not enter the registration page
    {
      console.log("auth");
    this.router.navigate(['/']);
    }
    

    console.log(this.todate);
    this.helper.get_time().subscribe(todate => {
      this.todate =  todate;
      console.log(this.todate);
      console.log(5);
    })
  }


  
  onSubmit(form: NgForm){
    const user = form.value;
    console.log(user);
    this.httpclient.post("http://localhost:3000/create_user",user).subscribe((data:any) => {
      console.log(data);
      if (data=="user added")
      {
      alert ("You have created a new account");
      this.router.navigate(["/"]);
      }
    })

  }
  


}
