import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { HelpService } from '../../help.service';
import { post } from 'selenium-webdriver/http';
import {ArticleService} from '../article.service';
import { AuthService } from '../../auth/auth.service';

@Component({
  selector: 'app-article-list',
  templateUrl: './article-list.component.html',
  styleUrls: ['./article-list.component.css']
})
export class ArticleListComponent implements OnInit {
  result=[{}];
  begin:boolean;

  constructor(private articleService: ArticleService,private helper:HelpService, private router:Router,private authService: AuthService) { }

  ngOnInit() {

    this.articleService.load_articles().subscribe(  result2 => {
      
      this.begin=true;

       result2.forEach(article => {
         article["article_time"] = new Date (article["article_time"]);

       });
       this.result= result2;
       console.log(this.result);
   });
  }

  goto_create_article() {
    this.router.navigate(['/','create-article']);
  }
  
}
