import { Injectable } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import { Router } from "@angular/router";
import { Subject,Observable, observable } from "rxjs";

// this service is for usefull functions which might be relevant to many pages.
@Injectable({ providedIn: "root" })
export class ArticleService {

//   private authStatusListener = new Subject<boolean>();

  constructor(private httpclient: HttpClient, private router: Router) {}

get_time() { //getting the current time

  return   new Observable<any>(observer => {
    this.httpclient.get("http://localhost:3000/current_time").subscribe((data:any) => {
      observer.next(data.result);
  }
);
    
  }
);


}

submit_article (article_title: string,article_html: string, article_content: string) {
  return   new Observable<any>(observer => {
    this.httpclient.post("http://localhost:3000/create_article",{article_title,article_html, article_content}).subscribe((data:any) => {
      observer.next(data.result);
  }
);
    
  }
);

}


load_articles () {
  console.log();
  return   new Observable<any>(observer => {
    this.httpclient.get("http://localhost:3000/load_articles").subscribe((data:any) => {
      observer.next(data.result);
  }
);
    
  }
);

}

  article_view(article_id: number) {

    return new Observable<any>(observer => {
      this.httpclient.post("http://localhost:3000/get_article", { article_id: article_id }).subscribe((data: any) => {
        observer.next(data.result);
      }
      );

    }
    );

  }




}
