import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router, Params } from '@angular/router';
import { HttpClient } from '@angular/common/http';
import { ArticleService } from '../article.service';


@Component({
  selector: 'app-view-article',
  templateUrl: './view-article.component.html',
  styleUrls: ['./view-article.component.css']
})
export class ViewArticleComponent implements OnInit {
  article_id: number = 0;
  article:{};

  constructor(private route: ActivatedRoute, private router: Router, private httpclient: HttpClient,private articleService: ArticleService) { }

  ngOnInit() {

    this.route.params
    .subscribe(
      (params: Params) => {
        try {
          this.article_id = +params['article_id'];
          console.log(this.article_id);
          const isint = Number.isInteger(this.article_id)
          if (!isint)
            this.router.navigate(['/']);
          this.articleService.article_view(this.article_id).subscribe(article => {
            this.article = article;
            console.log(this.article);
            if (!this.article)
            this.router.navigate(['/']);
          })

        }

        catch (err) {
          console.log(err);
          this.router.navigate(['/']);
        }
      }
    );
  }

}
