

export class Tag {
  public tag_id: Number;
  public tag_name: string;
  public tagging_id: Number;
  public tag_category:string
  public tag_subcategory:string;


  constructor(tag_id: Number,tag_name: string,tagging_id: Number,tag_category:string,tag_subcategory:string) {
      this.tag_id=tag_id;
      this.tag_name=tag_name;
      this.tagging_id=tagging_id;
      this.tag_category=tag_category;
      this.tag_subcategory= tag_subcategory;

  }


}
