isNaturalNumber (str) {
  var pattern = /^(0|([1-9]\d*))$/;
  return pattern.test(str);
}

isPositiveNaturalNumber (str) {
  var pattern = /^(([1-9]\d*))$/;
  return pattern.test(str);
}



