import { Component, OnInit, ElementRef, ViewChild } from '@angular/core';
import {COMMA, ENTER} from '@angular/cdk/keycodes';
import {FormControl} from '@angular/forms';
import {MatAutocompleteSelectedEvent, MatChipInputEvent,MatAutocomplete} from '@angular/material';
import {MatIconModule} from '@angular/material/icon';
import {Observable} from 'rxjs';
import {map, startWith} from 'rxjs/operators';
import 'hammerjs';
import { NgForm } from '@angular/forms';
import { forEach } from '@angular/router/src/utils/collection';
import {HttpClient} from '@angular/common/http';
import { htmlAstToRender3Ast } from '@angular/compiler/src/render3/r3_template_transform';

@Component({
  selector: 'app-post-tags',
  templateUrl: './post-tags.component.html',
  styleUrls: ['./post-tags.component.css']
})
export class PostTagsComponent implements OnInit {
  visible = true;
  selectable = true;
  removable = true;
  addOnBlur = true;
  separatorKeysCodes: number[] = [ENTER, COMMA];
  tagCtrl = new FormControl();
  filteredtags: Observable<string[]>;
  tags= [];
  all_tags=[];
  tags_full_data=[];
  @ViewChild('tagInput') element: ElementRef;

  @ViewChild('tagInput') tagInput: ElementRef<HTMLInputElement>;
  @ViewChild('auto') matAutocomplete: MatAutocomplete;

  constructor(private httpclient: HttpClient) {
    this.filteredtags = this.tagCtrl.valueChanges.pipe(
        startWith(null),
        map((tag: string | null) => tag ? this._filter(tag) : this.all_tags.slice()));
  }

  check() {
    console.log(this.tags);
    
    console.log(this.element);
  }

  add(event: MatChipInputEvent): void {
    // Add tag only when MatAutocomplete is not open
    // To make sure this does not conflict with OptionSelected Event
    if (!this.matAutocomplete.isOpen) {
      const input = event.input;
      const value = event.value;
      // is it real tag and not already used one?
     const istag= this.all_tags.indexOf(value)>-1 && this.tags.indexOf(value)==-1;
     console.log(istag);
      // Add our tag
      if (istag && this.tags.length<=4)
      {
      if ((value || '').trim()) {
        this.tags.push(value.trim());
      }
    }

      // Reset the input value
      if (input) {
        input.value = '';
      }

      this.tagCtrl.setValue(null);
    }
  }

  remove(tag: string): void {
    const index = this.tags.indexOf(tag);

    if (index >= 0) {
      this.tags.splice(index, 1);
    }
  }

  selected(event: MatAutocompleteSelectedEvent): void {
    console.log(this.tags);
    console.log(this.tags.length);
    if (this.tags.indexOf(event.option.viewValue)==-1  && this.tags.length<=4)
    {
    this.tags.push(event.option.viewValue);
    this.tagInput.nativeElement.value = '';
    this.tagCtrl.setValue(null);
    }
  }

  private _filter(value: string): string[] {
    const filterValue = value.toLowerCase();

    // return this.all_tags.filter(tag => tag.toLowerCase().indexOf(filterValue) === 0);
    let tags1 = this.all_tags.filter(tag => tag.toLowerCase().indexOf(filterValue) === 0);
    console.log(tags1);
    return tags1;
  }


  ngOnInit() {

        //getting the tags
        this.httpclient.get("http://localhost:3000/tags/",{params: {kind: "all", seperated: "false"}}).subscribe((data:any) => {
          // console.log(data);
          this.tags_full_data=data;
          console.log(this.tags_full_data);
          data.forEach(tag => {
            this.all_tags.push(tag.tag_name);
          });
          console.log(this.all_tags);
    
        })


  }




}
